// ------------------------------------------------
// Socials Styles Start
// ------------------------------------------------

.socials {
  position: absolute;
  bottom: 3.2rem;
  width: 100%;
  padding: 0 3.2rem;
  @include clearfix;
  opacity: 0;
  transition: all .3s ease-in-out;
  &.type-4 {
    position: absolute;
    left: 0;
    bottom: 3.2rem; }

  ul {
    display: block;
    position: relative;
    margin: 0;
    padding: 0;
    @include clearfix;
    li {
      display: block;
      float: left;
      margin: 0 3rem 0 0;
      &:last-of-type {
        margin: 0; }

      a {
        display: block;
        position: relative;
        width: 2.4rem;
        height: 2.4rem;
        line-height: 2.4rem;
        text-align: center;
        font-size: 1.6rem;
        color: #191919;
        transition: color .3s;
        &.light-text {
          color: $txt-white; }
        i {
          font-size: 2.5rem; } }

      .no-touch &:hover {
        a {
          color: $txt-light; } } } }

  @include MQ(M) {
    // opacity: 1
    &.type-4 {
      width: auto;
      position: absolute;
      // left: auto
      @include center(x);
      bottom: auto;
      right: 3.2rem;
      top: 3.2rem;
      padding: 0; } }

  @include MQ(XL) {
    opacity: 1;
    width: auto;
    padding: 0;
    @include position(absolute, 3.2rem 3.2rem auto null); }

  @include MQ(XXL) {
    opacity: 1;
    @include position(absolute, 4rem 4rem null null); } }

.footer-content {
  .socials {
    text-align: center;
    ul {
      display: inline-block; }
    @include MQ(XL) {
      @include position(relative, auto auto null null); }

    @include MQ(XXL) {
      @include position(relative, auto auto null null); } } }
// ------------------------------------------------
// Socials Styles End
// ------------------------------------------------


