// ------------------------------------------------
// Main Section Styles Start
// ------------------------------------------------

.home {
  min-height: 480px;
  overflow-x: hidden; }

.home_intro,
.home_media {
  position: relative;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.bgdarkblue {
  background-color: rgba(0, 0, 0, 0);
  background-color: rgba(27,34,62,0.95);
  background-blend-mode: multiply; }

.bglightblue {
  background-color: rgba(0, 0, 0, 0);
  background-color: rgba(64,180,231,0.95);
  background-blend-mode: multiply; }

.home_media {
  height: 600px;
  opacity: 0;
  transition: opacity .6s ease-in-out;
  @include MQ(M) {
    height: 760px; }
  @include MQ(XL) {
    height: 100%; } }

.home_intro {
  padding: 70px 0;
  opacity: 1;
  transition: all .3s;
  &.type-4 {
    padding: 0; }
  &.is-hidden {
    .logo,
    .socials,
    .headline {
      opacity: 0; } }

  @include MQ(XL) {
    padding: 0; } }

.logo {
  position: relative;
  top: 3.2rem;
  @include center(x);
  z-index: 998;
  opacity: 0;
  transition: all .3s ease-in-out;
  text-align: center !important;
  img {
    max-width: 80%;
 }    // margin-top: 10rem
  .mobile {
    display: block; }
  .decktop {
    display: none; }
  @include MQ(XL) {
    position: relative;
    transform: translateX(0);
    margin-top: 10rem;
    margin-bottom: 5rem;
    top: 3.2rem;
    left: 0;
    .mobile {
      display: none; }
    .decktop {
      display: block; } }
  @include MQ(XXL) {
    top: 4rem;
    left: 1rem; } }

.infoicons {

  margin-top: 2rem;
  img {
    max-height: 100px;
    height: auto; } }

#rotate {
  // background-color: green
  height: 5rem;
  // width: 100%
  position: relative;
  div {
    // background-color: yellow
    width: 70%;
    margin: 0 auto;
    height: 5rem;
    p {
      top: 0;
      height: 5rem;
      font-size: 4rem;
      line-height: 5.5rem;
      font-weight: bold;
      float: left;
      margin-right: 0.3rem; }
    b {
      height: 5rem;
      float: left;
      overflow: hidden;
      position: relative; }

    span {
      height: 5rem;
      font-size: 4rem;
      line-height: 5.5rem;
      display: inline-block;
      color: #000;
      position: relative;
      white-space: nowrap;
      text-align: center;
      top: 0;
      left: 0;
      margin-left: 1rem;
      margin-right: 1rem;
      animation: move 8s;
      animation-iteration-count: infinite;
      animation-delay: 1s; } }

  @keyframes move {
    0% {
      top: 0; } //produce
    25% {
      top: -5.5rem; } //distribute
    50% {
      top: -11rem; } //finance
    75% {
      top: -16.5rem; } } } //support


.headline {
  position: relative;
  width: 100%;
  padding: 3rem 10% 0 10%;
  opacity: 1;
  transition: all .3s ease-in-out;

  h1, p, a {
    opacity: 0;
    text-align: center; }
  h3 {
    text-align: center; }
  @include MQ(XL) {
    // position: absolute
    padding: 0 10rem;
    left: 0;
    // +center(y)
    &.headline-bottom {
      position: absolute;
      top: auto;
      left: 0;
      bottom: 8rem;
      padding: 0 10rem;
      transform: translateY(0); } }
  @include MQ(XXL) {
    // position: absolute
    padding: 0 14rem;
    left: 0;
    // +center(y)
    &.headline-bottom {
      top: auto;
      left: 0;
      bottom: 12rem;
      padding: 0 14rem;
      transform: translateY(0); } } }

// ------------------------------------------------
// Main Section Styles End
// ------------------------------------------------

#brands {
  img {
      max-width: 7rem;
      height: auto;
      padding: 1rem;
      border: 1px solid #efefef;
      border-radius: .5rem;
      @include MQ(XL) {
        max-width: 8rem; }
      @include MQ(XXL) {
        max-width: 8rem; } } }
